/* Estilos generales */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #ffffff;
    background-image: url('../imagen/admin.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    max-width: 500px;
    margin: 0 auto;
    padding: 50px;
}
.h1{
    color: #ffffff;
    border-color: #00000000;
}
.btn-container {
    margin-bottom: 20px;
    text-align: right;
}

.btn-container button {
    padding: 10px 20px;
    background-color: #000000;
    border: none;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
}

.btn-container button:hover {
    background-color: #535354;
}

h1 {
    color: #333;
}

.section {
    background-color: #a09e9ed6;
    padding: 20px;
    border-radius: 60px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
}

h2 {
    color: #333;
}

form {
    margin-bottom: 20px;
}

input[type="text"],
input[type="email"],
input[type="tel"],
select,
input[type="password"] {
    padding: 20px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 60px;
}

select {
    width: calc(100% - 22px);
}

button[type="submit"] {
    width: 100%;
    padding: 10px;
    background-color: #000000;;
    border: none;
    border-radius: 60px;
    color: #fff;
    cursor: pointer;
}

button[type="submit"]:hover {
    background-color: #585656;;
}

#usuariosContainer {
    margin-top: 60px;
    background-color: #a09e9ed6;
    padding: 20px;
    border-radius: 60px;
}

.edit, .delete{
    width: 100%;
    padding: 10px;
    background-color: #000000;;
    border: none;
    border-radius: 60px;
    color: #fff;
    cursor: pointer;
    margin: 10px;
}

.delete:hover {
    background-color: #585656;;
}
.edit:hover {
    background-color: #585656;;
}